import { mount } from 'enzyme';
import React from 'react';
import { defaultValues } from '../../constants/defaultValues';
import { Button } from './Button';

const mockClick = jest.fn();
export const Component = (
  isChecked,
  isDefaultChecked,
  isDisabled,
  isFullWidth,
  isRound,
  size,
  type,
  value
) => {
  return (
    <Button
      isDisabled={isDisabled}
      isFullWidth={isFullWidth}
      isRound={isRound}
      size={size}
      type={type}
      onClick={mockClick}
    >
      {value}
    </Button>
  );
};

let output;

export const tests = button => {
  if (button) {
    output = mount(button);
  } else {
    output = mount(
      Component(
        defaultValues.isChecked,
        defaultValues.isDefaultChecked,
        defaultValues.isDisabled,
        defaultValues.isFullWidth,
        defaultValues.isRound,
        defaultValues.size,
        defaultValues.type,
        defaultValues.value
      )
    );
  }


};

tests();
